export default {
	name: "inputCheck",
	props: {
		required: String,
		pattern: RegExp,
		exist: String,
		maxlength: String,
		minlength: String
	},
	data() {
		return {
			msg: "hello world123",
			error: false,
			title: "",
			requiredError: false,
			patternError: false,
			maxlengthError: false,
			minlengthError: false,
			existError: false,
		}
	},
	methods: {
		toCheck(pattern, title) {
			console.log(this.maxlength);
			if(!title) {
				this.error = true;
				this.requiredError = true;
				this.patternError = false;
				this.maxlengthError = false;
				this.minlengthError = false;
				this.existError = false;
				return;
			} else {
				this.error = false;
				this.requiredError = false;
			}
			if(!pattern.test(title)) {
				this.error = true;
				this.patternError = true;
				this.maxlengthError = false;
				this.minlengthError = false;
				this.existError = false;
				return;
			} else {
				this.error = false;
				this.patternError = false;
			}
			if(title.length > this.maxlength) {
				this.error = true;
				this.maxlengthError = true;
				this.minlengthError = false;
				this.existError = false;
				return;
			} else {
				this.error = false;
				this.maxlengthError = false;
			}
			if(title.length < this.minlength) {
				this.error = true;
				this.minlengthError = true;
				this.existError = false;
				return;
			} else {
				this.error = false;
				this.minlengthError = false;
			}
			if(this.exist) {
				this.error = true;
				this.existError = true;
				return;
			} else {
				this.error = false;
				this.existError = false;
			}
		}
	}
}
